@import url("https://fonts.googleapis.com/css?family=Montserrat&display=swap");

/*Color o Imagen Body*/
$FondoBody: #212529;
$FondoDegradeBody: radial-gradient(ellipse at top, #212529, transparent), radial-gradient(ellipse at bottom, #000000, transparent);;
$FondoImagenBody: linear-gradient(rgba(0, 0, 0, var(--opacidad-negro)), rgba(0, 0, 0, var(--opacidad-negro))), url("../img/fondo00.jpeg");

/*Colores Menu */
$FondoMenu: #00000017;
$FondoImagenMenu: radial-gradient(circle at right top, #00000017, #212529, #0e3d7a);
$TextoBotonesMenu: #ffffff;
$FondoBotonActivadoMenu: #0000ff;
$TextoBotonesActivadoMenu: #ffffff;
$transicionBotonMenu: 1.5s;

/*Colores Menu Mobile*/
$Boton3rayitas: #ffffff;
$FondoMenuMobile:#0e1133cc;
$TextoMenuMobile: #ffffff;
$FondoImagenMenuMobile: radial-gradient(circle at right top, #c3ec50, #212529, #037409);

/*Colores Tarjetas */
$TituloTarjeta: #fff;
$SombraTituloTarjeta: -6px 8px 10px #7fff07;
$FondoTarjeta: #000000;
$SombraTarjeta: 0px 1px 10px #00000033;
$BordeTarjeta: #09ee0934;


$SombraHoverTarjeta: 10px 10px 30px #60f80775;
$FondoHoverTitulo: #001aff;

$SubtituloTarjeta: #fff;
$SombraSubtituloTarjeta: -6px 8px 10px rgb(127, 255, 7);

$ParrafoTarjeta: #fff;
$BordesTablaTarjeta: #28c928;
$TextoTablaTarjeta: #fff;


$BotonFondoTarjeta: #2fb4cc;
$BotonBordeTarjeta: #09ee09;
$BotonHoverFondoTarjeta:  #09ee09;
$BotonHoverTextoTarjeta: #000000;;


/*Colores Tarjetas generadas con Ciclos*/
$coloresBox: (
    Color01: #114561e5,
    Color02: #3411b4,
    Color03: #3bd112,
    Color04: #d718e4,
    Color05: #b211c423,
);

/*Otras Variables de Tarjetas*/

/*Titulos Tarjetas*/
$TamanoTextotarjtas: 1rem;
$AnchoTituloTarjetas: 100%;
$AnchoTituloHoverTarjetas: 90%;
$MaximoAnchoTituloTarjetas: 1080px;


/*Tarjetas*/
$AnchoTarjetas: 100%;
$AlineacionTextoTarjetas: center;


/*Imagenes Tarjetas*/
$AnchoImagenTarjetas: 100%;
$AltoImagenTarjetas: 291px;
$TamanoTextoSubtituloTarjeta: 1.5rem;









